import React from "react";
import {Button, Form, Input, InputNumber, Radio} from "antd";
const formItemLayout = {
    labelCol: {span:2},
    wrapperCol: {span:16},
};
const tailFormItemLayout = {
    wrapperCol: {offset:2},
};
export default class JudgementSubjectEditor extends React.Component{
    onFinish=(values)=>{
        if(!this.props.onFinish) return;
        this.props.onFinish({type: "", ...values});

    };
    render() {
        return (
            <div >
                <Form onFinish={this.onFinish} initialValues={this.props.dataSource} name="create-judgement" {...formItemLayout}>
                    <Form.Item name="score" label="" rules={[{required: true,message: ''},]}>
                        <InputNumber min={1} max={100}/>
                    </Form.Item>
                    <Form.Item name="question" label=""
                               rules={[{required: true,message: ''},]}>
                        <Input.TextArea  rows={5}/>
                    </Form.Item>
                    <Form.Item name="answer" label="" rules={[{required: true,message: ''},]}>
                        <Radio.Group>
                            <Radio value={'1'}></Radio>
                            <Radio value={'0'}></Radio>
                        </Radio.Group>
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit">
                            
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        )
    }
}